@import url('https://fonts.googleapis.com/css?family=Dancing+Script');

:root {
  --header-bg: rgb(255, 208, 0);
  --navtab-hover: rgb(255, 226, 98);
}

.App {
  text-align: center;
}

.App h1,
.App h2 {
  color: #444;
  font-family: 'Dancing Script', serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  overflow: hidden;
  background-color: var(--header-bg);
  /* height: 150px; */
  padding: 0px;
  color: rgb(44, 44, 44);
  position: relative;
}

.App-section {
  padding: 80px 0;
}
.App-section.even {
  background-color: #eee;
}

.App-title {
  font-size: 2.0em;
  font-weight: 100;
  font-family: 'Dancing Script', sans-serif;
}
.App-title-desc {
  font-size: 1.0em;
  color: #777;
}

.App-nav-tabs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}
.App-nav-tab {
  display: -ms-flexbox;
  display: flex;
  padding: 10px;
  font-size: 10pt;
  color: #222!important;
  -ms-flex-align: center;
      align-items: center;
}
.App-nav-tab:hover {
  background-color: var(--navtab-hover);
}

.App-fixed-header {
  background-color: var(--header-bg);
  position: fixed;
  top: -100px;
  -webkit-transition: top .5s ease;
  -o-transition: top .5s ease;
  transition: top .5s ease;
  left: 0;
  right: 0;
  z-index: 100;
}
.App-fixed-header.show {
  top: 0;
}
.App-fixed-header .container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.App-flex-space {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
}
.App-fixed-header .App-github-link {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  padding: 8px;
}
.App-fixed-header .App-title {
  font-size: 14pt;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-item-align: start;
      align-self: flex-start;
  padding: 10px;
  color: #222;
}

.App-intro {
  font-size: large;
}

.App-github-link {
  position: absolute;
  top: 0;
  right: 0;
}
.App-github-link-decor {
  position: absolute;
  top: -55px;
  right: -55px;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: #fff;
  width: 100px;
  height: 100px;
}
.App-github-link svg {
  width: 30px;
  height: 30px;
  display: block;
  margin: auto;
}
.App-proj-item {
  margin: 40px 0;
  color: #777;
  /* background-color: #0af; */
}
.App-port-item {
  margin: 40px 0;
  color: #777;
  /* background-color: #0af; */
}
.App-port-item-screenshot {
  /* height: 200px; */
  overflow: hidden;
}
.App-port-item-screenshot img {
  /* margin: -200px; */
  width: 100%;
}

.App-block {
  margin: 20px;
}
.App-block > * {
  margin: 8px 0;
}
.App-refs-title-desc {
  color: #888;
}
.App-refs-item-desc {
  font-size: 0.9em;
  color: #777;
}
.App-refs-item-btn {
  font-size: 0.7em;
}
.App-refs-item-name {
  color: #444;
}

.App-skill-item {
  margin: 40px 0;
}
.App-skill-item-name {
  color: #777;
}
.App-skill-item svg {
  width: 40px;
}
.App-skill-item p {
  margin: 5px 0;
  font-size: 0.8em;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (min-width: 576px) {
  .App-title {
    font-size: 2.5em;
  }
}
@media only screen and (max-width: 575px) {
  .hidden-sm {
    display: none;
  }
}


